import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';

import './navbar.css';
import AboutMe from '../AboutMe';
import MyWork from '../MyWork';

export default function NavBar() {
  const [active, setActive] = useState('about-me');

  useEffect(() => {
    if (localStorage.getItem('activePage') !== null) {
      setActive(localStorage.getItem('activePage'));
    }
  }, []);

  const handlePage = (pageName) => {
    setActive(pageName);
    localStorage.setItem('activePage', pageName);
  };
  return (
    <Router>
      <section className='navigation'>
        <div className='links'>
          <div
            className={
              active === 'about-me' ? 'circleBehind active' : 'circleBehind'
            }
            onClick={() => handlePage('about-me')}
          >
            <Link to='/portfolio/'>About Me</Link>
          </div>
          <div
            className={
              active === 'my-work' ? 'circleBehind active' : 'circleBehind'
            }
            onClick={() => handlePage('my-work')}
          >
            <Link to='/portfolio/my-work'>My Work</Link>
          </div>
        </div>
      </section>

      <Switch>
        <Route exact path='/portfolio/'>
          <AboutMe />
        </Route>
        <Route path='/portfolio/my-work'>
          <MyWork />
        </Route>
      </Switch>
    </Router>
  );
}
